* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-size: 0.875rem;
  /* letter-spacing: 0.15rem; */
}

a {
  text-decoration: none;
  color: inherit;
}

ul,
ol {
  list-style: none;
}

button {
  background: none;
  border: none;
  font-size: inherit;
  color: inherit;
  letter-spacing: inherit;
}
